#clap {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  /* min-height: 100vh; */
  font-family: Montserrat;
}

.clap {
  position: relative;
  outline: 1px solid transparent;
  /* left: 80px; */

  /* border-radius: 50%; */
  /* border: 1px solid rgba(189, 195, 199, 1); */
  width: 80px;
  height: 80px;
  /* background: none; */
}
.clap:hover {
  cursor: pointer;
  /* border: 1px solid rgba(39, 174, 96, 1); */
  transition: border-color 0.3s ease-in;
  cursor: pointer;
}
.clap:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  border-radius: 50%;
  width: 79px;
  height: 79px;
}
.clap:hover:after {
  -webkit-animation: shockwave 1s ease-in infinite;
          animation: shockwave 1s ease-in infinite;
}
.clap svg {
  width: 40px;
  fill: none;
  stroke: rgba(39, 174, 96, 1);
  stroke-width: 2px;
}
.clap svg.checked {
  fill: rgba(39, 174, 96, 1);
  stroke: #fff;
  stroke-width: 1px;
}
.clap .clap--count {
  position: absolute;
  top: -50px;
  left: 20px;
  font-size: 0.8rem;
  color: white;
  background: rgba(39, 174, 96, 1);
  border-radius: 50%;
  height: 40px;
  width: 40px;
  line-height: 40px;
}
.clap .clap--count-total {
  position: absolute;
  font-size: 1 em;
  width: 80px;
  text-align: center;
  /* right:20px; */
  top: 0px;
  color: rgba(189, 195, 199, 1);
}

