@-webkit-keyframes shockwave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0 0 2px rgba(39, 174, 96, 1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
    box-shadow: 0 0 50px rgba(20, 91, 50, 1),
      inset 0 0 10px rgba(39, 174, 96, 1);
  }
}
@keyframes shockwave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0 0 2px rgba(39, 174, 96, 1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
    box-shadow: 0 0 50px rgba(20, 91, 50, 1),
      inset 0 0 10px rgba(39, 174, 96, 1);
  }
}

